
.cart-items {
    list-style: none;
    margin: 0;
    padding: 0;
  }

.total{
    display: flex;
    justify-content: space-between;
    margin: 1rem 0;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
}


.actions {
    text-align: right;
  }
  
.actions button {
    font: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #8a2b06;
    padding: 0.5rem 2rem;
    border-radius: 25px;
    margin-left: 1rem;
  }
  
.actions button:hover,
.actions button:active {
    background-color: #5a1a01;
    border-color: #5a1a01;
    color: white;
  }
  
.actions .button--alt {
    color: #8a2b06;
}
  
.actions .button {
    background-color: #8a2b06;
    color: white;
}

.cartList{
  overflow-y: scroll;
  min-height: 0rem;
  max-height: 18rem;
}


